
export const vectorComputeaAgle = (centerVector, relativeVector) => {
  // va * vb = |a| * |b| * cos<a,b>
  const aL = Math.sqrt(centerVector.x * centerVector.x + centerVector.y * centerVector.y)
  const bL = Math.sqrt(relativeVector.x * relativeVector.x + relativeVector.y * relativeVector.y)
  if (aL === 0 || bL === 0) {
    console.warn(centerVector, relativeVector, '向量长度为0')
    return 180
  }
  const cos = (centerVector.x * relativeVector.x + centerVector.y * relativeVector.y) / aL / bL
  const ratate = parseFloat((Math.acos(cos) / Math.PI) * 180)

  return ratate
}

export const cos = (vector, angle, isAbs = true) => {
  isAbs && (angle = angle < 0 ? -angle : angle)
  // 求向量的余弦值
  // π = 180
  return (
    Math.cos((angle * Math.PI) / 180) *
    Math.sqrt(vector.x * vector.x + vector.y * vector.y)
  )
}

export const sin = (vector, angle, isAbs = true) => {
  isAbs && (angle = angle < 0 ? -angle : angle)
  // 求向量的余弦值
  // π = 180
  return (
    Math.sin((angle * Math.PI) / 180) *
    Math.sqrt(vector.x * vector.x + vector.y * vector.y)
  )
}

export const vectorForRotate = (vector, angle) => {
  const arc = (angle * Math.PI) / 180
  const x = vector.y * Math.sin(arc) + vector.x * Math.cos(arc)
  const y = vector.y * Math.cos(arc) - vector.x * Math.sin(arc)
  return {
    x,
    y
  }
}



