<template lang="pug">
  .text-editor(
    v-if="isEditor && selectElementData.type == 'text-element'"
    :style="elementStyle"
  )
    .compute-block(
      contenteditable="true"
      ref="computeBlock"
      @focus="onComputeBlockFocus"
      @blur="onComputeBlockBlur"
      @keydown="onComputeBlockKeyDown"
    )
      span(ref="textRender") {{selectElementData.text}}
</template>
<script>
import { mapState, mapGetters } from 'vuex'
import { antiShake } from '@/utils/util'

export default {
  name: 'TextEditor',
  props: {
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters(['isEditor']),
    ...mapState({
      selectElementId: state => state.element.selectElementId,
      elementList: state => state.element.elementList,
      selectElementData () {
        const selectElementData = this.elementList.find(item => item.elementId == this.selectElementId)
        return selectElementData || {}
      }
    }),
    elementStyle () {
      if (this.selectElementData && this.selectElementData.style) {
        return this.selectElementData.style
      } else {
        return {}
      }
    }
  },
  watch: {
    isEditor (bool) {
      if (bool && this.selectElementData.type == 'text-element') {
        this.$nextTick(() => {
          this.$refs.computeBlock.focus()
        })
      }
    }
  },
  mounted () {
  },
  methods: {
    onComputeBlockFocus () {
      this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: this.selectElementData, vuexCaptureMark: 'start' })
      // 选中内容
      const computeBlock = this.$refs.computeBlock
      if (window.getSelection) {
        const range = document.createRange()
        range.selectNodeContents(computeBlock)
        window.getSelection().removeAllRanges()
        window.getSelection().addRange(range)
      }
    },
    onComputeBlockKeyDown (ev) {
      const computeBlock = this.$refs.computeBlock
      antiShake(() => {
        const width = computeBlock.offsetWidth + 'px'
        const height = computeBlock.offsetHeight + 'px'
        let style = this.selectElementData.style || {}
        style = Object.assign(style, {
          height,
          width
        })
        this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(this.selectElementData, { style }) })
      }, 0)
    },
    onComputeBlockBlur () {
      const computeBlock = this.$refs.computeBlock
      const selectElementData = this.selectElementData
      const text = computeBlock.innerText
      selectElementData.text = text
      this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: selectElementData, vuexCaptureMark: 'end' })
      this.$store.dispatch('setIsEditor', false)
    }
  }
}
</script>
<style lang="stylus" scoped>
.text-editor
  z-index 100001
  position absolute
  .compute-block
    outline none
    width 100%
    height 100%
    user-select text
    cursor text
    width 100%
    height auto
</style>



