<template lang="pug">
  .operate(
    v-show="showOperate"
    :style="operateStyle"
    :class="[selectElementId && !isMove && !isRotate ? 'operate-active' : isMove ? 'operate-move' : '']"
    @dblclick="ondblclick"
  )
    .rotate-btn(
      ref="rotate_btn"
      v-show="showRotateBtn"
    )
      .rotate-icon
        span(class="no-user-select" v-show="isRotate" :style="{transform: `rotateZ(${-rotateDeg}deg)`}") {{rotateDeg}}°
    .dot(
      v-show="showDotBtn"
      v-for="(item, index) in position"
      :key="index"
      v-direction="true"
      :class="[getDirection(index, elementStyle && elementStyle.rotate), item]")
</template>
<script>
import { mapState, mapGetters } from 'vuex'
import { vectorDefault, directionVectors } from '@/factory/constant'
import { vectorComputeaAgle, cos } from '@/factory/tool'
import { bindMove } from '@/factory/server'
import { antiShake } from '@/utils/util'

export default {
  name: 'Operate',
  directives: {
    direction: {
      bind (el, binding, vnode) {
        const self = vnode.context
        const ob = {
          direction: '',
          element: {
            minWidth: 14,
            minHeight: 14,
            width: 0,
            height: 0,
            originWidth: 0,
            originHeight: 0,
            top: 0,
            left: 0,
            fontSize: 0,
            lineHeight: 0
          },
          coverImg: {
            x: 0,
            y: 0,
            width: 0,
            height: 0
          },
          center: {},
          startPoint: {
            x: 0,
            y: 0
          }
        }
        bindMove(el,
          (ev) => {
            ev.stopPropagation()
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: self.selectElementData, vuexCaptureMark: 'start' })
            const w = self.elementStyle && self.elementStyle.width && parseFloat(self.elementStyle.width)
            const h = self.elementStyle && self.elementStyle.height && parseFloat(self.elementStyle.height)
            self.resetDirectionVectors(w, h)
            const directionName = el.classList[2]
            ob.direction = directionName.replace('dot-', '')
            ob.element.width = self.elementStyle && self.elementStyle.width ? parseFloat(self.elementStyle.width) : 0
            ob.element.height = self.elementStyle && self.elementStyle.height ? parseFloat(self.elementStyle.height) : 0
            ob.element.originWidth = self.elementStyle && self.elementStyle.originWidth ? parseFloat(self.elementStyle.originWidth) : 0
            ob.element.originHeight = self.elementStyle && self.elementStyle.originHeight ? parseFloat(self.elementStyle.originHeight) : 0
            ob.element.left = self.elementStyle && self.elementStyle.left ? parseFloat(self.elementStyle.left) : 0
            ob.element.top = self.elementStyle && self.elementStyle.top ? parseFloat(self.elementStyle.top) : 0
            ob.coverImg.width = self.selectElementData.coverImg && self.selectElementData.coverImg.width ? parseFloat(self.selectElementData.coverImg.width) : 0
            ob.coverImg.height = self.selectElementData.coverImg && self.selectElementData.coverImg.height ? parseFloat(self.selectElementData.coverImg.height) : 0
            ob.coverImg.x = self.selectElementData.coverImg && self.selectElementData.coverImg.x ? parseFloat(self.selectElementData.coverImg.x) : 0
            ob.coverImg.y = self.selectElementData.coverImg && self.selectElementData.coverImg.y ? parseFloat(self.selectElementData.coverImg.y) : 0
            ob.element.fontSize = self.elementStyle && self.elementStyle.fontSize ? parseFloat(self.elementStyle.fontSize) : 0
            ob.element.lineHeight = self.elementStyle && self.elementStyle.lineHeight ? parseFloat(self.elementStyle.lineHeight) : 0
            ob.startPoint.x = ev.clientX
            ob.startPoint.y = ev.clientY
            ob.center = Object.assign({}, self.centerSpotPositon)
          },
          (ev) => {
            ev.stopPropagation()
            let targetCenter = null // 目标中心点位置
            let computedCenter = null // 计算中心点位置
            let angle = ''
            let angleX // 方向矢量的水平分解
            let angleY
            let distance = '' // 中心点移动的矢量距离
            let distanceY = '' // 垂直分解向量
            let distanceX = '' // 水平分解向量
            let centerMoveX
            let centerMoveY
            const direction = ob.direction
            let _direction = direction
            const transformStr = self.elementStyle && self.elementStyle.transform ? self.elementStyle.transform : ''
            const rotateNum = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
            const rotate = parseFloat(rotateNum)
            const x = ev.clientX - ob.startPoint.x
            const y = ob.startPoint.y - ev.clientY
            let moveAngle = vectorComputeaAgle(self.directionVectors.n, { x, y })
            let width = ob.element.width
            let height = ob.element.height
            if (x < 0) {
              moveAngle = 360 - moveAngle
            }
            const NorthBasedDirectionAngle = (rotate + self.directionVectors[direction].angle) % 360
            angle = moveAngle - NorthBasedDirectionAngle
            distance = cos({ x, y }, angle)
            switch (direction) {
              case 'n':
              case 's':
                _direction = direction + 'e'
                break
              case 'w':
              case 'e':
                _direction = 'n' + direction
                break
            }
            angleX = (rotate + self.directionVectors[_direction[1]].angle) % 360
            angleX = NorthBasedDirectionAngle - angleX
            distanceX = distance * Math.cos((angleX * Math.PI) / 180)

            angleY = (rotate + self.directionVectors[_direction[0]].angle) % 360
            angleY = NorthBasedDirectionAngle - angleY
            distanceY = distance * Math.cos((angleY * Math.PI) / 180)

            centerMoveX = NorthBasedDirectionAngle - 90 //  90 是 正X轴方向角度
            centerMoveX = distance * Math.cos((centerMoveX * Math.PI) / 180)
            centerMoveY = NorthBasedDirectionAngle - 180 // 180 是 负Y轴方向角度
            centerMoveY = distance * Math.cos((centerMoveY * Math.PI) / 180)

            targetCenter = {
              x: ob.center.x + centerMoveX / 2,
              y: ob.center.y + centerMoveY / 2
            }
            const WHRate = ob.element.width / ob.element.height
            width = width + distanceX
            height = height + distanceY
            if (width <= ob.element.minWidth) {
              width = ob.element.minWidth
              if (direction.length == 2) {
                height = ob.element.minWidth / WHRate
              }
            }
            if (height <= ob.element.minHeight) {
              height = ob.element.minHeight
              if (direction.length == 2) {
                width = ob.element.minHeight * WHRate
              }
            }
            let style = self.selectElementData.style
            style = Object.assign(style, {
              height: height + 'px',
              width: width + 'px'
            })
            let coverImg = Object.assign({}, self.selectElementData.coverImg || {})
            const type = self.selectElementData.type
            // 文本编辑模式
            if (type == 'text-element') {
              if (direction.length == 2) {
                // 缩放行为
                const lineHeight = ob.element.lineHeight
                const fontSize = parseFloat(ob.element.fontSize)
                const simpleLineHeight = lineHeight * fontSize
                const lineNumber = ob.element.height / simpleLineHeight
                let fontsize = height / lineNumber / lineHeight
                if (fontsize < 12) {
                  fontsize = 12
                  height = fontsize * lineHeight * lineNumber
                  width = WHRate * height
                }
                style.fontSize = fontsize + 'px'
                style.width = width + 'px'
                style.height = height + 'px'
              }
            } else if (type == 'image-element') {
              const whRate = ob.element.originWidth / ob.element.originHeight
              let coverImgWidth, coverImgHeight, coverImgX, coverImgY
              if (direction) {
                coverImgWidth = ob.coverImg.width
                coverImgHeight = ob.coverImg.height
                coverImgX = ob.coverImg.x
                coverImgY = ob.coverImg.y
                if (direction == 'e') {
                  if (width <= ob.element.width + Math.abs(ob.coverImg.x)) {
                    coverImgX = ob.coverImg.x + width - ob.element.width
                  } else {
                    coverImgWidth = ob.coverImg.width + width - ob.element.width - Math.abs(ob.coverImg.x)
                    coverImgHeight = coverImgWidth / whRate
                    coverImgX = 0
                    coverImgY = ob.coverImg.y / ob.coverImg.height * coverImgHeight
                  }
                } else if (direction == 'n') {
                  if (height > ob.coverImg.height - Math.abs(ob.coverImg.y)) {
                    coverImgHeight = height + Math.abs(ob.coverImg.y)
                    coverImgWidth = coverImgHeight * whRate
                    coverImgX = ob.coverImg.x / ob.coverImg.width * coverImgWidth
                  }
                } else if (direction == 'w') {
                  if (width > ob.coverImg.width - Math.abs(ob.coverImg.x)) {
                    coverImgWidth = width + Math.abs(ob.coverImg.x)
                    coverImgHeight = coverImgWidth / whRate
                    coverImgY = ob.coverImg.y / ob.coverImg.height * coverImgHeight
                  }
                } else if (direction == 's') {
                  if (height > ob.element.height + Math.abs(ob.coverImg.y)) {
                    coverImgHeight = height + ob.coverImg.height - ob.element.height - Math.abs(ob.coverImg.y)
                    coverImgWidth = coverImgHeight * whRate
                    coverImgX = ob.coverImg.x / ob.coverImg.width * coverImgWidth
                    coverImgY = 0
                  } else {
                    coverImgY = ob.coverImg.y + height - ob.element.height
                  }
                } else {
                  coverImgWidth = width / ob.element.width * ob.coverImg.width
                  coverImgHeight = height / ob.element.height * ob.coverImg.height
                  coverImgX = ob.coverImg.x / ob.coverImg.width * coverImgWidth
                  coverImgY = ob.coverImg.y / ob.coverImg.height * coverImgHeight
                }
                coverImg.width = coverImgWidth + 'px'
                coverImg.height = coverImgHeight + 'px'
                coverImg.x = coverImgX + 'px'
                coverImg.y = coverImgY + 'px' 
              }
            }
            computedCenter = {
              x: ob.element.left + width / 2 + 50,
              y: ob.element.top + height / 2 + 60
            }
            if (width > ob.element.minWidth && height > ob.element.minHeight) {
              if (targetCenter.x != computedCenter.x) {
                style.left = targetCenter.x - parseFloat(width) / 2 - 60 + 'px'
              }
              if (targetCenter.y != computedCenter.y) {
                style.top = targetCenter.y - parseFloat(height) / 2 - 50 + 'px'
              }
            }
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.selectElementData, { style, coverImg }) })
          },
          (ev) => {
            ev.stopPropagation()
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: self.selectElementData, vuexCaptureMark: 'end' })
          })
      }
    }
  },
  props: {
  },
  data () {
    return {
      directionVectors,
      position: [
        'dot-nw',
        'dot-n',
        'dot-ne',
        'dot-e',
        'dot-se',
        'dot-s',
        'dot-sw',
        'dot-w'
      ]
    }
  },
  computed: {
    ...mapGetters(['isMove', 'isRotate', 'isEditor']),
    ...mapState({
      selectElementId: state => state.element.selectElementId,
      hoverElementId: state => state.hoverElementId,
      elementList: state => state.element.elementList,
      selectElementData () {
        const selectElementData = this.elementList.find(item => item.elementId == this.selectElementId)
        return selectElementData || {}
      }
    }),
    showOperate () {
      if (this.selectElementId) {
        if (this.isEditor) {
          return this.selectElementData.type == 'text-element'
        } else {
          return true
        }
      }
    },
    elementStyle () {
      if (this.selectElementData && this.selectElementData.style) {
        return this.selectElementData.style
      } else {
        return undefined
      }
    },
    operateStyle () {
      const elementStyle = this.elementStyle || {}
      let n = 0
      let width = parseFloat(elementStyle.width)
      let height = parseFloat(elementStyle.height)
      let top = parseFloat(elementStyle.top)
      let left = parseFloat(elementStyle.left)
      if (this.selectElementId && !this.isMove && !this.isRotate) {
        n = 2
      } else if (this.isMove) {
        n = 1
      }
      width = width + n * 2
      height = height + n * 2
      top = top - n
      left = left - n
      return Object.assign({}, elementStyle, { width: width + 'px', height: height + 'px', top: top + 'px', left: left + 'px', opacity: 1 })
    },
    showRotateBtn () {
      if (this.isMove) {
        return false
      } else if (this.isRotate) {
        return true
      } else if (!this.isMove && !this.isRotate && (this.selectElementId != this.hoverElementId)) {
        return true
      } else if (this.selectElementId == this.hoverElementId) {
        return false
      } else {
        return false
      }
    },
    rotateDeg () {
      const transformStr = this.elementStyle && this.elementStyle.transform ? this.elementStyle.transform : ''
      const rotateNum = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
      return parseFloat(rotateNum) ? Math.ceil(parseFloat(rotateNum)) : 0
    },
    showDotBtn () {
      if (this.isMove) {
        return false
      } else if (this.isRotate) {
        return false
      } else if (!this.isMove && !this.isRotate && (this.selectElementId != this.hoverElementId)) {
        return true
      } else if (this.selectElementId == this.hoverElementId) {
        return false
      } else {
        return false
      }
    },
    centerSpotPositon () {
      let x, y
      if (this.elementStyle) {
        const top = parseFloat(this.elementStyle.top) + 50
        const left = parseFloat(this.elementStyle.left) + 60
        const width = parseFloat(this.elementStyle.width)
        const height = parseFloat(this.elementStyle.height)
        x = left + width / 2
        y = top + height / 2
      }
      return { x, y }
    }
  },
  mounted () {
    this.initMoveEffect()
    this.initRotateEffect()
  },
  methods: {
    // 从 nw 方向算起第n个的箭头方向
    getDirection(n, rotate) {
      rotate = rotate || 0
      const directions = [
        'scale-nw',
        'scale-n',
        'scale-ne',
        'scale-e',
        'scale-se',
        'scale-s',
        'scale-sw',
        'scale-w'
      ]
      const rotateN = Math.floor((rotate + 22.5) / 45)
      for (let i = 0; i < rotateN; i += 1) {
        directions.push(directions.shift())
      }
      return directions[n]
    },
    resetDirectionVectors(w, h) {
      // 根据宽高修改四个对角的向量方向，为了在缩放的时候按比例缩放
      const angle = (Math.atan(w / h) / Math.PI) * 180
      this.directionVectors.ne.angle = 0 + angle
      this.directionVectors.se.angle = 180 - angle
      this.directionVectors.sw.angle = 180 + angle
      this.directionVectors.nw.angle = 360 - angle
    },
    initRotateEffect () {
      const self = this
      const element = this.$refs.rotate_btn
      this.$nextTick(() => {
        bindMove(element,
          (ev) => {
            self.$store.dispatch('setIsRotate', true)
            ev.stopPropagation()
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: self.selectElementData, vuexCaptureMark: 'start' })
          },
          (ev) => {
            const relativeX = ev.clientX - self.centerSpotPositon.x
            const relativeY = self.centerSpotPositon.y - ev.clientY
            let rotate = vectorComputeaAgle(vectorDefault, { x: relativeX, y: relativeY })
            if (relativeX > 0) {
              rotate = 360 - rotate
            }
            // rotate = Math.ceil(rotate)
            if (rotate >= 180) {
              rotate = rotate - 360
            }
            let style = self.selectElementData.style || {}
            style = Object.assign(style, {
              rotate: rotate,
              transform: `rotateZ(${rotate == 360 ? 0 : rotate}deg)`
            })
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.selectElementData, { style }) })
          },
          (ev) => {
            const transformStr = self.elementStyle && self.elementStyle.transform ? self.elementStyle.transform : ''
            let rotate = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
            rotate = Math.ceil(rotate)
            let style = self.selectElementData.style || {}
            style = Object.assign(style, {
              rotate: rotate,
              transform: `rotateZ(${rotate == 360 ? 0 : rotate}deg)`
            })
            self.$store.dispatch('setIsRotate', false)
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.selectElementData, { style }), vuexCaptureMark: 'end' })
          })
      })
    },
    initMoveEffect () {
      let x = 0
      let y = 0
      let n = 0
      const self = this
      this.$nextTick(() => {
        const element = self.$el
        bindMove(element,
          (ev) => {
            if (self.isEditor) return
            n++
            antiShake(() => {
              if (n == 1) {
                self.$store.dispatch('setIsMove', true)
                n = 0
              } else if (n > 1) {
                n = 0
              }
            }, 100)
            x = ev.clientX - element.offsetLeft
            y = ev.clientY - element.offsetTop
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: self.selectElementData, vuexCaptureMark: 'start' })
          },
          (ev) => {
            if (self.isEditor) return
            const l = ev.clientX - x
            const t = ev.clientY - y
            let style = self.selectElementData.style
            style = Object.assign(style, {
              left: l + 'px',
              top: t + 'px'
            })
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.selectElementData, { style }) })
          },
          (ev) => {
            if (self.isEditor) return
            self.$store.dispatch('setIsMove', false)
            if (n == 1) {
              antiShake(() => {
                self.$store.dispatch('setIsMove', false)
              }, 100)
            }
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: self.selectElementData, vuexCaptureMark: 'end' })
          })
      })
    },
    ondblclick () {
      if (self.isEditor) return
      this.$store.dispatch('setIsEditor', true)
    }
  }
}
</script>
<style lang="stylus" scoped>
.operate-move
  border 1px dotted rgba(108,207,255,.3)
.operate-active
  border 2px dotted #6ccfff
$circle-btn-offset = -11px
.operate
  white-space normal
  word-wrap break-word
  z-index 100000!important
  box-sizing border-box
  transform-origin center center
  opacity 1
  cursor move
  .rotate-btn
    position absolute
    bottom -30px
    left 50%
    width 20px
    height 20px
    margin-left -10px
    cursor default
    border-radius 50%
    z-index 10000
    .rotate-icon
      background  url("data:image/svg+xml;charset=utf-8,%3Csvg width='25' height='24' xmlns='http://www.w3.org/2000/svg' fill='%23757575'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Ccircle stroke='%23CCD1DA' fill='%23FFF' cx='13' cy='12' r='11.5'/%3E%3Cpath d='M16.242 12.012a4.25 4.25 0 00-5.944-4.158L9.696 6.48a5.75 5.75 0 018.048 5.532h1.263l-2.01 3.002-2.008-3.002h1.253zm-8.484-.004a4.25 4.25 0 005.943 3.638l.6 1.375a5.75 5.75 0 01-8.046-5.013H5.023L7.02 9.004l1.997 3.004h-1.26z' fill='%23000' fill-rule='nonzero'/%3E%3C/g%3E%3C/svg%3E") no-repeat center center
      width 20px
      height 20px
      position relative
      background-size 100% 100%
      span
        font-weight 400
        font-style normal
        position absolute
        width 28px
        top: 40px
        text-align center
        left: calc(50% - 24px)
        transform translateX(-calc(50% - 24px))
        height 28px
        line-height 28px
        padding 0 8px
        font-size 12px
        background #0e1217
        border-radius 4px
        color #fff
  .dot
    position absolute
    width 20px
    height 20px
    background-color unset
    border none
    display flex
    justify-content center
    align-items center
    &.dot-sw
    &.dot-se
    &.dot-nw
    &.dot-ne
      &:before
        content ' '
        display block
        width 8px
        height 8px
        border-radius 6px
        background-color #fff
        border 1px solid rgba(42,134,255,0.4)

  .dot-se
    top $circle-btn-offset
    left $circle-btn-offset

  .dot-sw
    top $circle-btn-offset
    right $circle-btn-offset

  .dot-ne
    bottom $circle-btn-offset
    left $circle-btn-offset

  .dot-nw
    bottom $circle-btn-offset
    right $circle-btn-offset
  .dot-s,
  .dot-n,
  .dot-e,
  .dot-w
    &:before
      content ' '
      display block
      border-radius 6px
      background-color #fff
      border 1px solid rgba(42,134,255,0.4)
  .dot-s,
  .dot-n
    &:before
      width 12px
      height 6px
  .dot-e,
  .dot-w
    &:before
      width 6px
      height 12px
  .dot-s
    left 50%
    top $circle-btn-offset
    margin-left $circle-btn-offset

  .dot-w
    top 50%
    right $circle-btn-offset
    margin-top $circle-btn-offset

  .dot-n
    bottom $circle-btn-offset
    left 50%
    margin-left $circle-btn-offset

  .dot-e
    top 50%
    left $circle-btn-offset
    margin-top $circle-btn-offset

  .scale-nw
    cursor se-resize
  .scale-n
    cursor s-resize
  .scale-ne
    cursor sw-resize
  .scale-e
    cursor w-resize
  .scale-se
    cursor nw-resize
  .scale-s
    cursor n-resize
  .scale-w
    cursor e-resize
  .scale-sw
    cursor ne-resize
</style>



