<template lang="pug">
  .image-element(
    v-show="showelement"
    :class="[selectElementId != elementData.elementId ? 'no-user-select' : '']"
    :style="elementStyle"
    @click="selectElement"
    @mouseover="onmouseover"
  )
    img( :style="imgStyle" :src="elementData.imgSrc")
</template>
<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'ImageElement',
  props: {
    elementData: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {

    }
  },
  computed: {
    ...mapGetters(['isMove', 'isRotate', 'isEditor']),
    ...mapState({
      hoverElementId: state => state.hoverElementId,
      selectElementId: state => state.element.selectElementId
    }),
    elementStyle () {
      if (this.elementData && this.elementData.style) {
        return this.elementData.style
      } else {
        return undefined
      }
    },
    imgStyle () {
      const coverImgData = this.elementData.coverImg || {}
      const width = coverImgData.width
      const height = coverImgData.height
      const x = coverImgData.x
      const y = coverImgData.y
      return {
        width,
        height,
        transform: `translate(${x}, ${y})`
      }
    },
    showelement () {
      if (this.isEditor) {
        return this.elementData.elementId != this.selectElementId
      } else {
        return true
      }
    }
  },
  mounted () {
  },
  methods: {
    selectElement (e) {
      e.stopPropagation()
      if (this.elementData.elementId) {
        this.$store.dispatch('setElementId', this.elementData.elementId)
      }
    },
    onmouseover (e) {
      e.stopPropagation()
      if (this.elementData.elementId && !this.isMove && !this.isRotate && this.selectElementId != this.elementData.elementId) {
        this.$store.dispatch('setHoverElementId', this.elementData.elementId)
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.image-element
  opacity 1
  overflow hidden
  img {
    display block
    width 100%
  }
</style>



