<template lang="pug">
  .element-hover(
    :class="[isMove ? 'element-move' : '']"
    v-show="hoverElementId && !isEditor"
    :style="elementHoverStyle"
    @dblclick="ondblclick"
  )
    .block(@mouseout="blockmouseout")
</template>
<script>
import { mapState, mapGetters } from 'vuex'
import { bindMove } from '@/factory/server'

export default {
  name: 'ElementHover',
  props: {
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters(['isMove', 'isEditor']),
    ...mapState({
      hoverElementId: state => state.hoverElementId,
      elementList: state => state.element.elementList,
      hoverElementIdData () {
        const hoverElementIdData = this.elementList.find(item => item.elementId == this.hoverElementId)
        return hoverElementIdData || {}
      }
    }),
    elementHoverStyle () {
      const elementStyle = this.elementStyle || {}
      let top = parseFloat(elementStyle.top)
      let left = parseFloat(elementStyle.left)
      top = top - 1
      left = left - 1
      return Object.assign({}, elementStyle, { top: top + 'px', left: left + 'px' })
    },
    elementStyle () {
      if (this.hoverElementIdData && this.hoverElementIdData.style) {
        return this.hoverElementIdData.style
      } else {
        return {}
      }
    }
  },
  mounted () {
    this.initMoveEffect()
  },
  methods: {
    blockmouseout () {
      if (!this.isMove) {
        this.$store.dispatch('setHoverElementId', '')
      }
    },
    ondblclick (ev) {
      ev.stopPropagation()
    },
    initMoveEffect () {
      let x = 0
      let y = 0
      const self = this
      this.$nextTick(() => {
        const element = self.$el
        bindMove(element,
          (ev) => {
            self.$store.dispatch('setIsMove', true)
            x = ev.clientX - element.offsetLeft
            y = ev.clientY - element.offsetTop
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: self.hoverElementIdData, vuexCaptureMark: 'start' })
            self.$store.dispatch('setElementId', self.hoverElementId)
          },
          (ev) => {
            const l = ev.clientX - x
            const t = ev.clientY - y
            let style = self.hoverElementIdData.style || {}
            style = Object.assign(style, {
              left: l + 'px',
              top: t + 'px'
            })
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.hoverElementIdData, { style }) })
          },
          (ev) => {
            self.$store.dispatch('setIsMove', false)
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: self.hoverElementIdData, vuexCaptureMark: 'end' })
            self.$store.dispatch('setHoverElementId', '')
          })
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
.element-move
  border 1px dotted rgba(108,207,255,.3)!important
.element-hover
  position absolute
  border 1px solid #6ccfff
  cursor move
  .block
    width 100%
    height 100%
</style>



