<template lang="pug">
  .img-editor(
    class="no-user-select"
    :style="{zIndex: showImgEditor ? 100002 : 0}"
    v-show="showImgEditor"
  )
    .cover-area(:style="coverAreaStyle")
      .cover-img-block(:style="coverImgBlockStyle")
        div(:style="coverEchoArea")
          img(:style="coverAreaImgStyle" :src="selectElementData.imgSrc")
      span(v-arrow="true" class="cropper-cropbox-line point-e line-e")
      span(v-arrow="true" class="cropper-cropbox-line point-n line-n")
      span(v-arrow="true" class="cropper-cropbox-line point-w line-w")
      span(v-arrow="true" class="cropper-cropbox-line point-s line-s")
      span(v-arrow="true" class="cropper-cropbox-point point-ne")
      span(v-arrow="true" class="cropper-cropbox-point point-nw")
      span(v-arrow="true" class="cropper-cropbox-point point-sw")
      span(v-arrow="true" class="cropper-cropbox-point point-se")
    div(:style="coverAreaOperateStyle")
      .operate-area(:style="operateAreaStyle")
        img(id="imgEditorOperateArea" ref="operateArea" class="operate-area-img" :style="coverAreaImgStyle" :src="selectElementData.imgSrc")
    .dot-mask(:style="dotMaskStyle")
      .dot-mask-container(:style="dotMaskContainerStyle")
        .dot(
          v-for="(item, index) in position"
          v-direction="true"
          :key="index"
          :class="[getDirection(index, elementStyle && elementStyle.rotate), item]")
    .bg-mask
</template>
<script>
import { mapState, mapGetters } from 'vuex'
import { bindMove } from '@/factory/server'
import { vectorForRotate, vectorComputeaAgle, cos, sin } from '@/factory/tool'
import { directionVectors } from '@/factory/constant'
import { antiShake } from '@/utils/util'

export default {
  name: 'ImgEditor',
  directives: {
    arrow: {
      bind (el, binding, vnode) {
        const self = vnode.context
        const ob = {
          direction: '',
          element: {
            minWidth: 14,
            minHeight: 14,
            width: 0,
            height: 0,
            originWidth: 0,
            originHeight: 0,
            left: 0,
            top: 0
          },
          coverImg: {
            width: 0,
            height: 0,
            x: 0,
            y: 0
          },
          referVector: {
            x: 0,
            y: 0
          },
          center: {},
          startPoint: {
            x: 0,
            y: 0
          }
        }
        bindMove(el, (ev) => {
          ev.stopPropagation()
          ev.preventDefault()
          self.isOperateEditor = true
          const w = self.elementStyle && self.elementStyle.width && parseFloat(self.elementStyle.width)
          const h = self.elementStyle && self.elementStyle.height && parseFloat(self.elementStyle.height)
          self.resetArrowVectors(w, h)
          const directionName = el.classList[1]
          ob.direction = directionName.replace('point-', '')
          ob.element.width = self.elementStyle && self.elementStyle.width ? parseFloat(self.elementStyle.width) : 0
          ob.element.height = self.elementStyle && self.elementStyle.height ? parseFloat(self.elementStyle.height) : 0
          ob.element.originWidth = self.elementStyle && self.elementStyle.originWidth ? parseFloat(self.elementStyle.originWidth) : 0
          ob.element.originHeight = self.elementStyle && self.elementStyle.originHeight ? parseFloat(self.elementStyle.originHeight) : 0
          ob.element.left = self.elementStyle && self.elementStyle.left ? parseFloat(self.elementStyle.left) : 0
          ob.element.top = self.elementStyle && self.elementStyle.top ? parseFloat(self.elementStyle.top) : 0
          ob.startPoint.x = ev.clientX
          ob.startPoint.y = ev.clientY
          ob.coverImg.width = self.coverImgData && self.coverImgData.width ? parseFloat(self.coverImgData.width) : 0
          ob.coverImg.height = self.coverImgData && self.coverImgData.height? parseFloat(self.coverImgData.height) : 0
          ob.coverImg.x = self.coverImgData && self.coverImgData.x ? parseFloat(self.coverImgData.x) : 0
          ob.coverImg.y = self.coverImgData && self.coverImgData.y ? parseFloat(self.coverImgData.y) : 0
          ob.center = Object.assign({}, self.centerSpotPositon)
        }, (ev) => {
          ev.stopPropagation()
          let targetCenter = null // 目标中心点位置
          let computedCenter = null // 计算中心点位置
          let angle = ''
          let angleX // 方向矢量的水平分解
          let angleY
          let distance = '' // 中心点移动的矢量距离
          let distanceY = '' // 垂直分解向量
          let distanceX = '' // 水平分解向量
          let centerMoveX
          let centerMoveY
          let _direction = ob.direction
          const transformStr = self.elementStyle && self.elementStyle.transform ? self.elementStyle.transform : ''
          const rotateNum = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
          const rotate = parseFloat(rotateNum)
          const x = ev.clientX - ob.startPoint.x
          const y = ob.startPoint.y - ev.clientY
          let moveAngle = vectorComputeaAgle(self.arrowVectors.n, { x, y })
          let width, height, coverX, coverY, maxWidth, maxHeight
          if (x < 0) {
            moveAngle = 360 - moveAngle
          }
          const NorthBasedDirectionAngle = (rotate + self.arrowVectors[ob.direction].angle) % 360
          angle = moveAngle - NorthBasedDirectionAngle
          distance = cos({ x, y }, angle)
          switch (ob.direction) {
            case 'n':
            case 's':
              _direction = ob.direction + 'e'
              break
            case 'w':
            case 'e':
              _direction = 'n' + ob.direction
              break
          }
          angleX = (rotate + self.arrowVectors[_direction[1]].angle) % 360
          angleX = NorthBasedDirectionAngle - angleX
          distanceX = distance * Math.cos((angleX * Math.PI) / 180)

          angleY = (rotate + self.arrowVectors[_direction[0]].angle) % 360
          angleY = NorthBasedDirectionAngle - angleY
          distanceY = distance * Math.cos((angleY * Math.PI) / 180)

          centerMoveX = distance * Math.cos((NorthBasedDirectionAngle - 90) * Math.PI / 180)
          centerMoveY = distance * Math.cos((NorthBasedDirectionAngle - 180) * Math.PI / 180)

          width = ob.element.width + distanceX
          height = ob.element.height + distanceY
          let style = Object.assign({}, self.selectElementData.style)
          let coverImg = Object.assign({}, self.selectElementData.coverImg)
          if (ob.direction == 'nw') {
            coverX = ob.coverImg.x
            coverY = ob.coverImg.y
            maxWidth = ob.coverImg.width - Math.abs(ob.coverImg.x)
            maxHeight = ob.coverImg.height - Math.abs(ob.coverImg.y)
          } else if (ob.direction == 'sw') {
            coverX = ob.coverImg.x
            coverY = ob.coverImg.y + distanceY
            maxWidth = ob.coverImg.width - Math.abs(ob.coverImg.x)
            maxHeight = ob.element.height + Math.abs(ob.coverImg.y)
          } else if (ob.direction == 'se') {
            coverX = ob.coverImg.x + distanceX
            coverY = ob.coverImg.y + distanceY
            maxWidth = ob.element.width + Math.abs(ob.coverImg.x)
            maxHeight = ob.element.height + Math.abs(ob.coverImg.y)
          } else if (ob.direction == 'ne') {
            coverX = ob.coverImg.x + distanceX
            coverY = ob.coverImg.y
            maxWidth = ob.element.width + Math.abs(ob.coverImg.x)
            maxHeight = ob.coverImg.height - Math.abs(ob.coverImg.y)
          } else if (ob.direction == 'n') {
            coverX = ob.coverImg.x
            coverY = ob.coverImg.y
            width = ob.element.width
            maxWidth = ob.element.width
            maxHeight = ob.coverImg.height - Math.abs(ob.coverImg.y)
          } else if (ob.direction == 's') {
            coverX = ob.coverImg.x
            coverY = ob.coverImg.y + distanceY
            maxHeight = ob.element.height + Math.abs(ob.coverImg.y)
          } else if (ob.direction == 'w') {
            coverX = ob.coverImg.x
            coverY = ob.coverImg.y
            maxWidth = ob.coverImg.width - Math.abs(ob.coverImg.x)
          } else if (ob.direction == 'e') {
            coverX = ob.coverImg.x + distanceX
            coverY = ob.coverImg.y
            maxWidth = ob.element.width + Math.abs(ob.coverImg.x)
          }
          if (width < 0) {
            width = 0
          } else if (width > maxWidth) {
            width = maxWidth
            if (ob.direction == 'nw') {
              coverX = ob.coverImg.x
            } else if (ob.direction == 'sw') {
              coverX = ob.coverImg.x
            } else if (ob.direction == 'se') {
              coverX = 0
            } else if (ob.direction == 'ne') {
              coverX = 0
            } else if (ob.direction == 'e') {
              coverX = 0
            }
          }
          if (height < 0) {
            height = 0
          } else if (height > maxHeight) {
            height = maxHeight
            if (ob.direction == 'nw') {
              coverY = ob.coverImg.y
            } else if (ob.direction == 'sw') {
              coverY = 0
            } else if (ob.direction == 'se') {
              coverY = 0
            } else if (ob.direction == 'ne') {
              coverY = ob.coverImg.y
            } else if (ob.direction == 'n') {
              coverY = ob.coverImg.y
            } else if (ob.direction == 's') {
              coverY = 0
            } 
          }
          targetCenter = {
            x: ob.center.x + centerMoveX / 2,
            y: ob.center.y + centerMoveY / 2
          }
          computedCenter = {
            x: ob.element.left + width / 2 + 50,
            y: ob.element.top + height / 2 + 60
          }

          if (targetCenter.x != computedCenter.x) {
            style.left = targetCenter.x - parseFloat(width) / 2 - 60 + 'px'
          }
          if (targetCenter.y != computedCenter.y) {
            style.top = targetCenter.y - parseFloat(height) / 2 - 50 + 'px'
          }
        
          coverImg.x = coverX + 'px'
          coverImg.y = coverY + 'px'
          style.width = width + 'px'
          style.height = height + 'px'
          self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.selectElementData, { style, coverImg }), vuexCaptureMark: 'noCapture' })
        }, (ev) => {
          ev.preventDefault()
          ev.stopPropagation()
          antiShake(() => { self.isOperateEditor = false }, 100)
        } )
      }
    },
    direction: {
      bind (el, binding, vnode) {
        const self = vnode.context
        const ob = {
          direction: '',
          element: {
            width: 0,
            height: 0,
            originWidth: 0,
            originHeight: 0,
          },
          coverImg: {
            width: 0,
            height: 0,
            x: 0,
            y: 0
          },
          referVector: {
            x: 0,
            y: 0
          },
          center: {},
          startPoint: {
            x: 0,
            y: 0
          }
        }
        bindMove(el,
          (ev) => {
            ev.stopPropagation()
            ev.preventDefault()
            self.isOperateEditor = true
            const w = self.coverImgData && self.coverImgData.width && parseFloat(self.coverImgData.width)
            const h = self.coverImgData && self.coverImgData.height && parseFloat(self.coverImgData.height)
            self.resetDirectionVectors(w, h)
            const directionName = el.classList[2]
            ob.direction = directionName.replace('dot-', '')
            ob.element.width = self.elementStyle && self.elementStyle.width ? parseFloat(self.elementStyle.width) : 0
            ob.element.height = self.elementStyle && self.elementStyle.height ? parseFloat(self.elementStyle.height) : 0
            ob.element.originWidth = self.elementStyle && self.elementStyle.originWidth ? parseFloat(self.elementStyle.originWidth) : 0
            ob.element.originHeight = self.elementStyle && self.elementStyle.originHeight ? parseFloat(self.elementStyle.originHeight) : 0
            ob.startPoint.x = ev.clientX
            ob.startPoint.y = ev.clientY
            ob.coverImg.width = w
            ob.coverImg.height = h
            ob.coverImg.x = self.coverImgData && self.coverImgData.x ? parseFloat(self.coverImgData.x) : 0
            ob.coverImg.y = self.coverImgData && self.coverImgData.y ? parseFloat(self.coverImgData.y) : 0
            ob.center = Object.assign({}, self.imgCenterSpotPositon)
            const transformStr = self.elementStyle && self.elementStyle.transform ? self.elementStyle.transform : ''
            const rotate = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
            ob.referVector = vectorForRotate({ x: 0, y: -1 }, rotate)
          },
          (ev) => {
            const x = ev.clientX - ob.startPoint.x
            const y = ob.startPoint.y - ev.clientY
            const WHRate = ob.element.originWidth / ob.element.originHeight
            let relativeRotate = vectorComputeaAgle(ob.referVector, { x, y })
            if (ob.referVector.x * y - x * ob.referVector.y > 0) {
              relativeRotate = -relativeRotate
            }
            const coverX = ob.coverImg.x - sin({ x, y }, relativeRotate, false)
            const coverY = ob.coverImg.y + cos({ x, y }, relativeRotate, false)
            let _coverX, _coverY, width, height, minWidth, minHeight
            if (ob.direction == 'nw') {
              _coverX = ob.coverImg.x
              _coverY = ob.coverImg.y
              width = ob.coverImg.width + coverX - ob.coverImg.x
              height = ob.coverImg.height + (coverX - ob.coverImg.x) / WHRate
              minWidth = ob.element.width + Math.abs(ob.coverImg.x)
              minHeight = ob.element.height + Math.abs(ob.coverImg.y)
            } else if (ob.direction == 'sw') {
              _coverX = ob.coverImg.x
              _coverY = coverY
              height = ob.coverImg.height - (coverY - ob.coverImg.y)
              width = ob.coverImg.width - (coverY - ob.coverImg.y) * WHRate
              minWidth = ob.element.width + Math.abs(ob.coverImg.x)
              minHeight = ob.coverImg.height - Math.abs(ob.coverImg.y)
            } else if (ob.direction == 'se') {
              _coverX = ob.coverImg.x - sin({ x, y }, relativeRotate, false)
              _coverY = ob.coverImg.y - sin({ x, y }, relativeRotate, false) / WHRate
              width = ob.coverImg.width - (_coverX - ob.coverImg.x)
              height = ob.coverImg.height - (_coverY - ob.coverImg.y)
              minWidth = ob.coverImg.width - Math.abs(ob.coverImg.x)
              minHeight = ob.coverImg.height - Math.abs(ob.coverImg.y)
            } else if (ob.direction == 'ne') {
              _coverX = coverX
              _coverY = ob.coverImg.y
              width = ob.coverImg.width - (coverX - ob.coverImg.x)
              height = ob.coverImg.height - (coverX - ob.coverImg.x) / WHRate
              minHeight = ob.element.height + Math.abs(ob.coverImg.y)
              minWidth = ob.coverImg.width - Math.abs(ob.coverImg.x)
            }
            if (width <= minWidth) {
                width = minWidth
                height = minWidth / WHRate
                if (ob.direction == 'nw') {
                  _coverX = ob.coverImg.x
                  _coverY = ob.coverImg.y
                } else if (ob.direction == 'sw') {
                  _coverX = ob.coverImg.x
                  _coverY = ob.coverImg.height - Math.abs(ob.coverImg.y) - height
                } else if (ob.direction == 'se') {
                  _coverX = 0
                  _coverY = ob.coverImg.height - Math.abs(ob.coverImg.y) - height
                } else if (ob.direction == 'ne') {
                  _coverY = ob.coverImg.y
                  _coverX = 0
                }
              } else if (height <= minHeight) {
                height = minHeight
                width = minHeight * WHRate
                if (ob.direction == 'nw') {
                  _coverX = ob.coverImg.x
                  _coverY = ob.coverImg.y
                } else if (ob.direction == 'sw') {
                  _coverX = ob.coverImg.x
                  _coverY = 0
                } else if (ob.direction == 'se') {
                  _coverX = ob.coverImg.width - Math.abs(ob.coverImg.x) - width
                  _coverY = 0
                } else if (ob.direction == 'ne') {
                  _coverY = ob.coverImg.y
                  _coverX = ob.coverImg.width - Math.abs(ob.coverImg.x) - width
                }
              }
            if (width < minWidth || height < minHeight || _coverX > 0 || _coverY > 0) {
              return
            }
            
            let coverImg = self.selectElementData.coverImg
            coverImg = Object.assign(coverImg, {
              height: height + 'px',
              width: width + 'px',
              x: _coverX + 'px',
              y: _coverY + 'px'
            })
            self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.selectElementData, { coverImg }), vuexCaptureMark: 'noCapture' })
          },
          (ev) => {
            ev.preventDefault()
            ev.stopPropagation()
            antiShake(() => { self.isOperateEditor = false }, 100)
          })
      }
    }
  },
  props: {
  },
  data () {
    return {
      directionVectors,
      arrowVectors: directionVectors,
      position: [
        'dot-nw',
        'dot-ne',
        'dot-se',
        'dot-sw'
      ],
      isOperateEditor: false
    }
  },
  computed: {
    ...mapGetters(['isEditor']),
    ...mapState({
      selectElementId: state => state.element.selectElementId,
      elementList: state => state.element.elementList,
      selectElementData () {
        const selectElementData = this.elementList.find(item => item.elementId == this.selectElementId)
        return selectElementData || {}
      }
    }),
    centerSpotPositon () {
      let x, y
      if (this.elementStyle) {
        const top = parseFloat(this.elementStyle.top) + 50
        const left = parseFloat(this.elementStyle.left) + 60
        const width = parseFloat(this.elementStyle.width)
        const height = parseFloat(this.elementStyle.height)
        x = left + width / 2
        y = top + height / 2
      }
      return { x, y }
    },
    imgCenterSpotPositon () {
      let x, y
      const top = this.elementStyle.top ? parseFloat(this.elementStyle.top) + 50 : 0
      const left = this.elementStyle.left ? parseFloat(this.elementStyle.left) + 60 : 0
      const width = this.coverImgData.width ? parseFloat(this.coverImgData.width) : 0
      const height = this.coverImgData.height ? parseFloat(this.coverImgData.height) : 0
      x = left + width / 2
      y = top + height / 2
      return { x, y }
    },
    showImgEditor () {
      return this.isEditor && this.selectElementData.type == 'image-element'
    },
    elementStyle () {
      if (this.selectElementData && this.selectElementData.style) {
        return this.selectElementData.style
      } else {
        return {}
      }
    },
    coverAreaOperateStyle () {
      const o = Object.assign({}, this.coverAreaStyle)
      o.zIndex = 0
      return o
    },
    coverAreaStyle () {
      const elementStyle = this.elementStyle || {}
      const width = elementStyle.width
      const height = elementStyle.height
      const top = elementStyle.top
      const left = elementStyle.left
      const transform = elementStyle.transform
      return {
        width,
        height,
        top,
        left,
        zIndex: 10002,
        position: 'absolute',
        transform
      }
    },
    coverImgData () {
      const selectElementData = this.selectElementData || {}
      return selectElementData.coverImg || {}
    },
    coverAreaImgStyle () {
      const width = this.coverImgData.width
      const height = this.coverImgData.height
      const x = this.coverImgData.x
      const y = this.coverImgData.y
      return {
        width,
        height,
        transform: `translate(${x}, ${y})`
      }
    },
    coverImgBlockStyle () {
      const width = this.coverImgData.width
      const height = this.coverImgData.height
      return {
        width,
        height
      }
    },
    coverEchoArea () {
      const elementStyle = this.elementStyle || {}
      const width = elementStyle.width
      const height = elementStyle.height
      return {
        width,
        height,
        overflow: 'hidden'
      }
    },
    dotMaskStyle () {
      const elementStyle = this.elementStyle || {}
      const o = {}
      const top = elementStyle.top
      const left = elementStyle.left
      const transformStr = this.elementStyle && this.elementStyle.transform ? this.elementStyle.transform : ''
      const rotate = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
      const x = this.coverImgData.x
      const y = this.coverImgData.y
      const width = elementStyle.width
      const height = elementStyle.height
      o.zIndex = 3
      o.position = 'absolute'
      o.width = width
      o.height = height
      o.top = top
      o.left = left
      o.transform = `rotateZ(${rotate}deg)`
      return o
    },
    dotMaskContainerStyle () {
      const width = this.coverImgData.width
      const height = this.coverImgData.height
      const x = this.coverImgData.x
      const y = this.coverImgData.y
      return {
        width,
        height,
        transform: `translate(${x}, ${y})`
      }
    },
    operateAreaStyle () {
      const elementStyle = this.elementStyle || {}
      const width = elementStyle.width ? parseFloat(elementStyle.width) : 0
      const originWidth = elementStyle.originWidth ? parseFloat(elementStyle.originWidth) : 0
      const height = elementStyle.height ? parseFloat(elementStyle.height) : 0
      const originHeight = elementStyle.originHeight ? parseFloat(elementStyle.originHeight) : 0
      const top = elementStyle.top ? parseFloat(elementStyle.top) : 0
      const left = elementStyle.left ? parseFloat(elementStyle.left) : 0
      const whRate = originWidth / originHeight
      let imgW, imgH
      if (height <= width / whRate) {
        imgW = width
        imgH = width / whRate
      } else {
        imgW = height * whRate
        imgH = height
      }
      return {
        top: top + 'px',
        left: left + 'px',
        width: imgW + 'px',
        height: imgH + 'px'
      }
    }
  },
  watch: {
    showImgEditor (bool) {
      if (bool) {
        this.$nextTick(() => {
          this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: this.selectElementData, vuexCaptureMark: 'start' })
          document.addEventListener('click', this.clickListener, true)
        })
      } else {
        document.removeEventListener('click', this.clickListener, true)
      }
    }
  },
  mounted () {
    this.initOperateAreaMoveEffect()
  },
  methods: {
    // 从 nw 方向算起第n个的箭头方向
    getDirection(n, rotate) {
      rotate = rotate || 0
      const directions = [
        'scale-nw',
        'scale-ne',
        'scale-se',
        'scale-sw'
      ]
      const rotateN = Math.floor((rotate + 22.5) / 45)
      for (let i = 0; i < rotateN; i += 1) {
        directions.push(directions.shift())
      }
      return directions[n]
    },
    resetArrowVectors(w, h) {
      // 根据宽高修改四个对角的向量方向，为了在缩放的时候按比例缩放
      const angle = (Math.atan(w / h) / Math.PI) * 180
      this.arrowVectors.ne.angle = 0 + angle
      this.arrowVectors.se.angle = 180 - angle
      this.arrowVectors.sw.angle = 180 + angle
      this.arrowVectors.nw.angle = 360 - angle
    },
    resetDirectionVectors(w, h) {
      // 根据宽高修改四个对角的向量方向，为了在缩放的时候按比例缩放
      const angle = (Math.atan(w / h) / Math.PI) * 180
      this.directionVectors.ne.angle = 0 + angle
      this.directionVectors.se.angle = 180 - angle
      this.directionVectors.sw.angle = 180 + angle
      this.directionVectors.nw.angle = 360 - angle
    },
    clickListener (ev) {
      const target = ev.target
      if (target.id != 'imgEditorOperateArea' && !this.isOperateEditor) {
        ev.preventDefault()
        ev.stopPropagation()
        this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: this.selectElementData, vuexCaptureMark: 'end' })
        this.$store.dispatch('setIsEditor', false)
      }
    },
    initOperateAreaMoveEffect () {
      let x = 0
      let y = 0
      let originX = 0
      let originY = 0
      let elementWidth = 0
      let elementHeight = 0
      let coverWidth = 0
      let coverHeight = 0
      let rotate = 0
      let referVector
      const self = this
      this.$nextTick(() => {
        const element = this.$refs.operateArea
        bindMove(element, (ev) => {
          self.isOperateEditor = true
          x = ev.clientX
          y = ev.clientY
          originX = self.coverImgData.x ? parseFloat(self.coverImgData.x) : 0
          originY = self.coverImgData.y ? parseFloat(self.coverImgData.y) : 0
          elementWidth = self.elementStyle.width ? parseFloat(self.elementStyle.width) : 0
          elementHeight = self.elementStyle.height ? parseFloat(self.elementStyle.height) : 0
          coverWidth = self.coverImgData.width ? parseFloat(self.coverImgData.width) : 0
          coverHeight = self.coverImgData.height ? parseFloat(self.coverImgData.height) : 0
          const transformStr = self.elementStyle && self.elementStyle.transform ? self.elementStyle.transform : ''
          rotate = transformStr.match(/rotateZ\((\S*)deg\)/) ? transformStr.match(/rotateZ\((\S*)deg\)/)[1] : 0
          referVector = vectorForRotate({ x: 0, y: -1 }, rotate)
        }, (ev) => {
          const relativeX = ev.clientX - x
          const relativeY = y - ev.clientY
          let coverImg = self.selectElementData.coverImg
          let relativeRotate = vectorComputeaAgle(referVector, { x: relativeX, y: relativeY })
          if (referVector.x * relativeY - relativeX * referVector.y > 0) {
            relativeRotate = -relativeRotate
          }
          let pX = originX - sin({ x: relativeX, y: relativeY }, relativeRotate, false)
          let pY = originY + cos({ x: relativeX, y: relativeY }, relativeRotate, false)
          if (pX > 0) {
            pX = 0
          } else if (pX < elementWidth - coverWidth) {
            pX = elementWidth - coverWidth
          }
          if (pY < elementHeight - coverHeight) {
            pY = elementHeight - coverHeight
          } else if (pY > 0) {
            pY = 0
          }
          coverImg = Object.assign(coverImg, {
            x: pX + 'px',
            y: pY + 'px'
          })
          self.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign(self.selectElementData, { coverImg }), vuexCaptureMark: 'noCapture' })
        }, (ev) => {
          antiShake(() => { self.isOperateEditor = false }, 100)
        })
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
$circle-btn-offset = -11px
.img-editor
  position fixed
  top 51px
  left 61px
  right 0
  bottom 0
  .cover-area
    pointer-events none
    .cover-img-block
      width 100%
      height 100%
      overflow hidden
    .cropper-cropbox-line
      background-color #6ccfff
      position absolute
      display block
      width 100%
      height 100%
    .line-e
      top 0
      left -3px
      width 3px
      cursor w-resize
      pointer-events auto
    .line-n
      bottom -3px
      left 0
      height 3px
      cursor s-resize
      pointer-events auto
    .line-w
      top 0
      right -3px
      width 3px
      cursor e-resize
      pointer-events auto
    .line-s
      top -3px
      left 0
      height 3px
      cursor n-resize
      pointer-events auto
    .cropper-cropbox-point
      width 13px !important
      height 13px !important
      opacity 1
      background-color transparent
      position absolute
      display block
      pointer-events auto
      &:before
        content ""
        position absolute
        background-color #fff
      &:after
        content ""
        position absolute
        background-color #fff
    .point-sw
      top -4px
      right -4px
      border-radius 3px 2px 3px 2px
      cursor ne-resize
      &:before
        width 13px
        height 4px
        border-radius 4px 3px 0px 0px
      &:after
        width 4px
        height 13px
        right 0px
        border-radius 0px 3px 4px 0px
    .point-se
      top -4px
      left -4px
      border-radius 2px 3px 3px
      cursor nw-resize
      &:before
        width 4px
        height 13px
        border-radius 3px 0px 0px 4px
      &:after
        width 13px
        height 4px
        border-radius 3px 4px 0px 0px
        top 0px
    .point-ne
      bottom -4px
      left -4px
      border-radius 3px 3px 3px 2px
      cursor sw-resize
      &:before
        width 4px
        height 13px
        border-radius 4px 0px 0px 3px
      &:after
        height 4px
        width 13px
        border-radius 0px 0px 4px 3px
        bottom 0px
    .point-nw
      right -4px
      bottom -4px
      cursor se-resize
      border-radius 3px 3px 2px 3px
      &:before
        content " "
        width 4px
        height 13px
        border-radius 0px 4px 3px 0px
        right 0px
      &:after
        content " "
        width 13px
        height 4px
        border-radius 0px 0px 3px 4px
        bottom 0px
  .operate-area
    pointer-events auto
    cursor move
    .operate-area-img
      outline: 3px dashed rgba(0, 0, 0, 0.4)
  .dot-mask
    pointer-events none
    .dot-mask-container
      .dot
        pointer-events auto
        position absolute
        width 20px
        height 20px
        background-color unset
        border none
        display flex
        justify-content center
        align-items center
        &.dot-sw
        &.dot-se
        &.dot-nw
        &.dot-ne
          &:before
            content ' '
            display block
            width 8px
            height 8px
            border-radius 6px
            background-color #fff
            border 1px solid rgba(42,134,255,0.4)

      .dot-se
        top $circle-btn-offset
        left $circle-btn-offset

      .dot-sw
        top $circle-btn-offset
        right $circle-btn-offset

      .dot-ne
        bottom $circle-btn-offset
        left $circle-btn-offset

      .dot-nw
        bottom $circle-btn-offset
        right $circle-btn-offset
      .scale-nw
        cursor nwse-resize
      .scale-ne
        cursor nesw-resize
      .scale-se
        cursor nwse-resize
      .scale-sw
        cursor nesw-resize
  .bg-mask
    position: fixed
    top 0
    left 0
    right 0
    bottom 0
    z-index 10000
    background-color rgba(0, 0, 0, 0.4)
    pointer-events none
</style>



