import makaElement from './components/MakaElement.vue'
import imageElement from './components/ImageElement.vue'
import makaOperate from '@/components/operate.vue'
import makaElementHover from '@/components/ElementHover.vue'
import TextEditor from '@/components/TextEditor.vue'
import ImgEditor from '@/components/ImgEditor.vue'
import { mapState } from 'vuex'
import { clone } from '@/utils/util'

let num = 0

export default {
  name: 'H5Editor',
  data () {
    return {
      colorModel: '',
      rotateModel: '',
      haveUndo: false,
      haveRedo: false
    }
  },
  computed: {
    ...mapState({
      projectIndex: state => state.projectIndex,
      selectElementId: state => state.element.selectElementId,
      elementList: state => state.element.elementList
    }),
    selectElement () {
      const element = this.elementList.find(item => item.elementId == this.selectElementId)
      return element || null
    }
  },
  components: {
    makaElement,
    makaOperate,
    imageElement,
    makaElementHover,
    TextEditor,
    ImgEditor
  },
  mounted () {
  },
  created () {
  },
  methods: {
    renderComonentType (item) {
      if (item.type == 'text-element') {
        return 'maka-element'
      } else if (item.type) {
        return item.type
      }
      return 'maka-element'
    },
    colorChange (value) {
      if (this.selectElement) {
        let style = clone(this.selectElement.style || {})
        style = Object.assign(style, {
          color: this.colorModel
        })
        this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign({}, this.selectElement, { style }) })
      }
    },
    rotateChange () {
      if (this.selectElement) {
        let style = clone(this.selectElement.style || {})
        style = Object.assign(style, {
          transform: this.rotateModel
        })
        this.$store.dispatch('setElementListItemCapture', { type: 'editor', element: Object.assign({}, this.selectElement, { style }) })
      }
    },
    addImageElement () {
      num++
      const ImageElement = {
        type: 'image-element',
        imgSrc: 'https://img1.maka.im/user/600666913/images/411016_65c9ab7157d41ed686d4b535e1a73297.jpg?x-oss-process=image/resize,w_1240',
        elementId: num,
        style: {
          margin: 0,
          position: 'absolute',
          top: '60px',
          left: '10px',
          opacity: 1,
          transform: 'rotateZ(0deg)',
          width: '150px',
          height: '225px',
          originWidth: '1240px',
          originHeight: '1860px',
          cursor: 'move'
        },
        coverImg: {
          width: '150px',
          height: '225px',
          x: 0,
          y: 0
        }
      }
      this.$store.dispatch('setElementListItemCapture', { type: 'add', element: ImageElement })
    },
    addElement (number) {
      const defaultElement = {
        type: 'text-element',
        text: '默认元素',
        elementId: 0,
        style: {
          color: '#000',
          margin: 0,
          position: 'absolute',
          top: '60px',
          left: '10px',
          transform: 'none',
          opacity: 1,
          verticalAlign: 'middle',
          transform: 'rotateZ(0deg)',
          textAlign: 'center',
          fontSize: '24px',
          fontWeight: 'normal',
          fontStyle: 'normal',
          textDecoration: 'none',
          lineHeight: '1.5',
          width: '150px',
          height: '36px'
        }
      }
      const ns = number || 1
      const arr = []
      for (let i = 0; i < ns; i++) {
        num++
        defaultElement.elementId = num
        const o = clone(defaultElement)
        arr.push(o)
      }
      this.$store.dispatch('setElementListItemCapture', { type: 'add', element: arr })
    }
  }
}



