<template lang="pug">
  .element(
    v-show="showelement"
    :class="[selectElementId != elementData.elementId && !isEditor ? 'no-user-select' : '']"
    :style="elementStyle"
    @click="selectElement"
    @mouseover="onmouseover"
  ) {{elementData.text}}
</template>
<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'MakaElement',
  props: {
    elementData: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {}
  },
  computed: {
    ...mapGetters(['isMove', 'isRotate', 'isEditor']),
    ...mapState({
      hoverElementId: state => state.element.hoverElementId,
      selectElementId: state => state.element.selectElementId
    }),
    elementStyle () {
      if (this.elementData && this.elementData.style) {
        return this.elementData.style
      } else {
        return undefined
      }
    },
    showelement () {
      if (this.isEditor) {
        return this.elementData.elementId != this.selectElementId
      } else {
        return true
      }
    }
  },
  mounted () {
  },
  methods: {
    selectElement (e) {
      e.stopPropagation()
      if (this.elementData.elementId) {
        this.$store.dispatch('setElementId', this.elementData.elementId)
      }
    },
    onmouseover (e) {
      e.stopPropagation()
      if (this.elementData.elementId && !this.isMove && !this.isRotate && this.selectElementId != this.elementData.elementId) {
        this.$store.dispatch('setHoverElementId', this.elementData.elementId)
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.element
  opacity 1
  white-space normal
  word-wrap break-word
</style>



