<template lang="pug">
  .h5-editor.clear
    .statistics
    component(:is="renderComonentType(item)" v-for="(item, index) in elementList", :key="item.elementId", :element-data="item")
    maka-operate
    maka-element-hover
    text-editor
    img-editor
</template>
<script>
import index from './index'

export default {
  ...index
}
</script>
<style lang="stylus" scoped>
.h5-editor
  position relative
  padding-top 50px
  width 100%
  height 100%
  box-sizing border-box
  border 1px solid #ccc
  background-color #f0f3f4
  .opreate-block
    position absolute
    top 0
    left 0
    height 50px
    width 100%
    box-shadow 1px 1px 2px #ccc
  .statistics
    position absolute
    top 5px
    right 5px
</style>



