export const bindMove = (element, mousedownCallback, mousemoveCallback, mouseupCallback) => {
  let start
  const step = (timestamp, ev) => {
    if (!start && timestamp) {
      start = timestamp
    }
    const elapsed = timestamp ? timestamp - start : 0
    mousemoveCallback && mousemoveCallback(ev)
    if (elapsed < 150) { // 在两秒后停止动画
      window.requestAnimationFrame(_timestamp => step(_timestamp, ev))
    }
  }
  if (element) {
    if (element.setCapture) { // 判断是否存在全局捕获
      element.onmousedown = function(ev) {
        mousedownCallback && mousedownCallback(ev)
        element.onmousemove = function(ev) {
          if (window.requestAnimationFrame) {
            window.requestAnimationFrame(timestamp => step(timestamp, ev))
          } else {
            mousemoveCallback && mousemoveCallback(ev)
          }
        }
        element.onmouseup = function(ev) {
          mouseupCallback && mouseupCallback(ev)
          element.releaseCapture()// 释放全局捕获
        }
        element.setCapture()
        return false // 防止火狐出现bug 解决火狐 谷歌 IE9以上的问题，（不能解决IE7下的问题）阻止默认事件
      }
    } else {
      element.onmousedown = function(ev) {
        mousedownCallback && mousedownCallback(ev)
        document.onmousemove = function(ev) {
          if (window.requestAnimationFrame) {
            step(undefined, ev)
          } else {
            mousemoveCallback && mousemoveCallback(ev)
          }
        }
        document.onmouseup = function(ev) {
          mouseupCallback && mouseupCallback(ev)
          document.onmousemove = null
          document.onmouseup = null
        }
        return false // 防止火狐出现bug 解决火狐 谷歌 IE9以上的问题，（不能解决IE7下的问题）阻止默认事件
      }
    }
  } else {
    console.warn('please delivery really parameter(element) to be use bindMove function')
  }
}

export const fixViewFocus = (callback) => {
  const timer = setTimeout(() => {
    clearTimeout(timer)
    callback && callback()
  }, 0)
}



