
// 默认相对中心点垂直负方向，向量长度1
export const vectorDefault = { x: 0, y: -1 }

/**
 * 左上顶点为坐标参考点
*/
export const directionVectors = {
  n: { x: 0, y: 1, angle: 180 },
  ne: { x: 1, y: 1, angle: 225 },
  e: { x: 1, y: 0, angle: 270 },
  se: { x: 1, y: -1, angle: 315 },
  s: { x: 0, y: -1, angle: 0 },
  sw: { x: -1, y: -1, angle: 45 },
  w: { x: -1, y: 0, angle: 90 },
  nw: { x: -1, y: 1, angle: 135 }
}



